@import "./clash-grotesk.css";
* {
  font-family: "ClashGrotesk-Variable", sans-serif;
}

.main-container {
  background-color: #2C2925;
  min-height: 100vh;
  padding: 12px 16px;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cv-block {
  display: flex;
  align-items: center;
  background-color: #37332F;
  justify-content: space-between;
  padding: 4px 16px 4px 4px;
  width: fit-content;
  border-radius: 1000px;
  border: 1px #2C2925;
}

.cv-block:hover {
  background-color: #423D38;
}
.cv-block:hover .arrow {
  margin-left: 16px;
  margin-right: 0;
}

.avatar {
  width: 48px;
  height: 48px;
  border-radius: 1000px;
}

.name-container {
  display: flex;
  align-items: center;
  margin-left: 16px;
}

.arrow {
  margin-left: 8px;
  margin-right: 8px;
}

.menu-buttons {
  display: flex;
  background-color: #37332F;
  border: 1px #2C2925;
  border-radius: 1000px;
  padding: 4px;
}
.menu-buttons div:first-child {
  background-color: #F6ECC9;
  color: #37332F;
  margin-right: -16px;
}
.menu-buttons div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #37332F;
  color: #F6ECC9;
  border-radius: 1000px;
  width: 112px;
  height: 48px;
}
.menu-buttons div:last-child {
  margin-left: -16px;
}

.main-blocks-container {
  margin-top: 12px;
  display: flex;
}

.left-main-block {
  position: relative;
  box-sizing: border-box;
  background-color: #F6ECC9;
  width: 1155px;
  height: 840px;
  padding: 56px 40px 16px 40px;
  border-radius: 16px;
}
.left-main-block .left-main-block-header h1 {
  margin: 0;
}
.left-main-block .paragraph-1 {
  margin-top: 64px;
  width: 825px;
}
.left-main-block .paragraph-2 {
  margin-top: 40px;
  width: 825px;
}
.left-main-block .explore-case-studies-button {
  position: absolute;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  border-radius: 1000px;
  background-color: #37332F;
  padding: 4px 24px 4px 4px;
  width: fit-content;
  color: #F6ECC9;
}
.left-main-block .explore-case-studies-button .explore-case-studies-arrow-down {
  background-color: #F6ECC9;
  border-radius: 1000px;
}
.left-main-block .explore-case-studies-button .explore-case-studies-arrow-down img {
  padding: 14px;
  height: 20px;
  width: 20px;
}
.left-main-block .explore-case-studies-button .explore-case-studies-text {
  margin-left: 16px;
}

.right-main-block {
  background-color: #37332F;
  width: 721px;
  height: 840px;
  border-radius: 16px;
  margin-left: 12px;
}

.main-block-footer {
  display: flex;
  justify-content: space-between;
  margin-top: 12px;
}
.main-block-footer .links-block-container {
  display: flex;
  color: #F6ECC9;
  background-color: #37332F;
  width: fit-content;
  border-radius: 1000px;
  padding: 4px;
}
.main-block-footer .links-block-container .link-block {
  padding: 12px 28px 12px 32px;
  display: flex;
  align-items: center;
}
.main-block-footer .links-block-container .link-block img {
  margin-left: 8px;
}
.main-block-footer .email-container {
  display: flex;
  align-items: center;
  color: #F6ECC9;
  border: 1px dashed #F6ECC9;
  border-radius: 1000px;
  padding: 4px 24px 4px 24px;
}
.main-block-footer .email-container img {
  width: 20px;
  height: 20px;
  margin-left: 16px;
}

.description-container {
  margin-top: 12px;
  color: #F6ECC9;
  background-color: #37332F;
  padding: 32px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
}
.description-container .i-love-container {
  width: 1115px;
}
.description-container .i-love-container .emotional-image {
  margin-top: -188px;
}
.description-container .i-love-container .human-friendly-image {
  margin-top: -176px;
  margin-left: 141px;
}
.description-container .expertise-container {
  margin-top: 120px;
  width: 681px;
  align-self: end;
}

.case-studies-header {
  color: #F6ECC9;
  background-color: #37332F;
  border-radius: 16px;
  margin-top: 12px;
  padding: 40px 48px;
  display: flex;
  align-items: center;
}
.case-studies-header img {
  margin-left: 40px;
}

.case-1-container {
  margin-top: 12px;
  background-color: #37332F;
  border-radius: 16px;
  padding: 16px;
  display: flex;
}
.case-1-container .left-block-container {
  display: flex;
  flex-direction: column;
}
.case-1-container .left-block-container .description-block-container {
  display: flex;
  align-items: stretch;
}
.case-1-container .left-block-container .text-container {
  background-color: #F6F5F3;
  border-radius: 16px;
  padding: 32px;
  margin-top: 4px;
  margin-left: 4px;
}
.case-1-container .left-block-container .header-container {
  background-color: #CDE0FE;
  border-radius: 16px;
  padding: 32px;
}
.case-1-container .left-block-container .header-container h3, .case-1-container .left-block-container .header-container div {
  width: 625px;
}
.case-1-container .left-block-container .col-1 {
  display: flex;
  flex-direction: column;
}
.case-1-container .left-block-container .outcome-container {
  flex-grow: 1;
}
.case-1-container .left-block-container .text-container h3, .case-1-container .left-block-container .text-container div {
  width: 398px;
}
.case-1-container .left-block-container .role-container ul {
  padding-left: 30px;
}
.case-1-container .right-block-container {
  padding-left: 16px;
}
.case-1-container .right-block-container .case-img {
  height: 720px;
  border-radius: 16px;
}
.case-1-container .right-block-container .tags-container {
  display: flex;
  margin-top: 4px;
}
.case-1-container .right-block-container .tags-container div {
  padding: 16px 20px 16px 20px;
  background-color: #423D38;
  border-radius: 29px;
  margin-left: -16px;
}
.case-1-container .right-block-container .tags-container div:first-child {
  margin-left: 0;
}
.case-1-container .right-block-container .link-container {
  display: flex;
  margin-top: 4px;
}
.case-1-container .right-block-container .link-container .year-block {
  padding: 16px 20px 16px 20px;
  background-color: #423D38;
  border-radius: 29px;
}
.case-1-container .right-block-container .link-container .link-block {
  padding: 16px 20px 16px 20px;
  border-radius: 29px;
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  background-color: #CDE0FE;
}

